<template>
    <div class="main" id="test">
        <div class="moon" style="" id="center">
            <canvas id="myCanvas" resize="true"></canvas>
            <img id="bg1" style="display: none" class="visuallyhidden" src="@/images/bg1.png" width="320" height="491">
            <img id="bg2" style="display: none" class="visuallyhidden" src="@/images/bg2.png" width="320" height="491">
            <img id="bg3" style="display: none" class="visuallyhidden" src="@/images/bg3.png" width="320" height="491">
            <img id="bg4" style="display: none" class="visuallyhidden" src="@/images/bg4.png" width="320" height="491">
        </div>
        <p class="bottom-desc"></p>
    </div>
</template>

<script>
    import paper from 'paper'
    import html2canvas from 'html2canvas'
    export default {
        name: "playpage",
        methods:{
            anmination(){
                paper.install(window)
                paper.setup(document.getElementById('myCanvas'))
                paper.settings.handleSize = 0;
                paper.settings.hitTolerance = 4;
            },
            paperStart(){
               // require('../js/myPaper.js')
                var h = window.outerHeight

                function circleWidth(width = 0.6){
                    if( h <= 580 ){
                        console.log('??')
                    }

                    var view_width = ( view.size.width/2 ) * width
                    return view_width
                }

                function opacityToZero(path,velocity = 0.01){
                    if(path.opacity <= 0.02){ path.opacity = 0;  return }
                    path.opacity -= velocity
                }

                function opacityToOne(path, velocity = 0.01){
                    path.opacity += velocity
                    if(path.opacity >= 0.9){ path.opacity = 1;  return }
                }

                function addCirclePoint(obj,times){
                    var hudu = (2*Math.PI / 360)
                    var x, y
                    for(var i = 0;i<times;i++){
                        var z = 360/times * i
                        x = Math.round( Math.cos(hudu*z)*circleWidth(1) +view.center.x)
                        y = Math.round( Math.sin(hudu*z)*circleWidth(1) +view.center.y)
                        var point = new Point(x, y)
                        obj.add(point)
                    }
                }

                function addCircleLine(segments,obj){
                    var list = []
                    for(var i = 0;i<90;i++){  //50 50 !!!!!!!
                        var path = new Path()
                        path.add(view.center)
                        path.lineTo(segments[i].point)
                        var ints = path.getIntersections(obj)
                        var ints_circle = path.getIntersections(outer_circle)
                        //console.log(ints[0].point)
                        if(0 in ints){
                            var link2 = new Path()
                            link2.strokeWidth = 2
                            link2.opacity = 0 //
                            link2.add(ints_circle[ints_circle.length - 1].point)
                            link2.lineTo(ints[ints.length - 1].point)
                            list.push(link2)
                            listLength += link2.length
                        }else{
                            var link = new Path()
                            link.strokeWidth = 2
                            link.opacity = 0 //
                            link.add(ints_circle[ints_circle.length - 1].point)
                            link.lineTo(view.center)
                            //list.push(link)
                            listLength += link.length
                        }
                    }

                    return list
                }

//
                var outer_circle = new Path.Circle({
                    center: view.center,
                    radius: circleWidth(),
                    strokeColor: '#F59A55',
                    strokeWidth: 6
                })

//
                var yd = new Path.Circle({
                    center: view.center,
                    radius: 6,
                    strokeColor: '#F59A55',
                    strokeWidth: 6,
                    fillColor: '#F59A55'
                })

//
                var outer_point = new Path()
                addCirclePoint(outer_point,90)
//outer_point.add(new Point(Math.cos(50)*circleWidth(),Math.sin(50)*circleWidth()))

//
                var pointer = new Path(view.center, new Point(view.center.x-circleWidth(),view.center.y))
                pointer.strokeColor = '#F59A55'
                pointer.strokeWidth = 6
                pointer.rotate(180, view.center)

//
                var users_path = new Path({
                    strokeColor: '#F59A55',
                    strokeWidth: 4
                })

                var user_point_list = 0

                function begin(){
                    opacityToZero(outer_circle, 0.02) //
                }


                view.onMouseDown = function (event) {
                    users_path.add(event.point)
                    console.log('dw')
                }

                view.onMouseDrag = function (event) {
                    users_path.add(event.point)
                    user_point_list += 1
                    console.log('drag')
                }


                var circle_line_list = []
                view.onMouseUp = function (){
                    users_path.closePath()
                    console.log(outer_point.segments)
                    circle_line_list = addCircleLine(outer_point.segments,users_path)
                    begin_result = true
                }

                var time = 0  //Init
                var hudu = 0 //
                var begin_result = false
                var begin_init = true
                var circle_line_list_length = 0
                var addmooncake_font = false
                var listLength = 0

                function randomNum(minNum,maxNum){
                    switch(arguments.length){
                        case 1:
                            return parseInt(Math.random()*minNum+1,10);
                            break;
                        case 2:
                            return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
                            break;
                        default:
                            return 0;
                            break;
                    }
                }
                view.onFrame = function onFrame(event){

                    if(!begin_result && begin_init){
                        begin()
                    }

                    if(begin_result){//
                        if(user_point_list <= 40){
                            pointer.opacity = 0 //
                        }

                        opacityToOne(outer_circle)
                        opacityToOne(yd)
                        if(hudu <360){
                            pointer.rotate(2.8, view.center)
                            hudu += 2.8
                        }
                        if(circle_line_list_length  != circle_line_list.length){

                        }else{
                            console.log('')
                            pointer.opacity = 0
                            begin_result = false //
                            addmooncake_font = true //
                        }
                        if(circle_line_list_length  <= circle_line_list.length / 1.5) opacityToZero(pointer,0.0083)

                        if(circle_line_list_length < circle_line_list.length){
                            opacityToOne( circle_line_list[circle_line_list_length] , 0.2)
                            //circle_line_list[circle_line_list_length].opacity += 0.3

                        }
                        time += event.delta
                        if(time >= 0.016001){
                            //
                            console.log(circle_line_list.length)
                            if(circle_line_list_length < circle_line_list.length){
                                circle_line_list[circle_line_list_length].strokeColor = '#F59A55'
                                circle_line_list_length += 1
                            }
                            time = 0
                        }



                    }

                    if(addmooncake_font){
                        //
                        //alert('bg'+randomNum(1,4))
                        var s = 'bg'+randomNum(1,4)
                        var pic_mooncake = new Raster(s)
                        pic_mooncake.position = view.center
                        pic_mooncake.size = new Size(circleWidth(0.74)*2, circleWidth(0.74)*2)
                        users_path.strokeColor = 'red'
                        var group = new Group([users_path, pic_mooncake])
                        group.setClipped(true)
                        group.fillColor = 'red'
                        addmooncake_font = false
                        begin_init = false


                        // pormiseok

                        endS(listLength)
                        changeToImage()
                        //console.log()


                        //var border = users_path.clone()

                        //
                        /*var outer_circle_border = new Path.Circle({
                            center: view.center,
                            radius: circleWidth(),
                            strokeColor: '#F59A55',
                            strokeWidth: 2
                        })*/

                        //var group2 = new Group([users_path, outer_circle])//
                        //group2.opacity = 0.5
                    }

                    //console.log(time)
                    //pointer.rotate(3, view.center)
                }

                function changeToImage(){
                    var dom = document.getElementById('test')
                    html2canvas(dom, {onclone: (document)=>{
                            document.getElementById('test').classList.add('changBG')
                            var svgImg = paper.project.exportSVG()
                            document.getElementById('test').innerHTML = ''
                            document.getElementById('test').appendChild(svgImg)

                        }}).then((canvas)=>{
                        localStorage.setItem('canvasSrc', canvas.toDataURL() )
                        window.location.href = window.location.href.split('#')[0] + '#/result'
                    })
                }

                function endS(s){
                    //alert(s)

                    switch(true){
                        case s<284:             localStorage.setItem('one','9');localStorage.setItem('lv','Lv9');localStorage.setItem('two',parseInt(  (  (284-s + 40) /284)*10 ) );     localStorage.setItem('wa', '');break;
                        case s<750 && s>=284:   localStorage.setItem('one','8');localStorage.setItem('lv','Lv8');localStorage.setItem('two',parseInt(  (  (750-s) /(750-284) )*10 ) );   localStorage.setItem('wa', '');break;
                        case s<1300 && s>=750:  localStorage.setItem('one','7');localStorage.setItem('lv','Lv7');localStorage.setItem('two',parseInt(  (  (1300-s) /(1300-750) )*10 ) ); localStorage.setItem('wa', '');break;
                        case s<2600 && s>=1300: localStorage.setItem('one','6');localStorage.setItem('lv','Lv6');localStorage.setItem('two',parseInt(  (  (2600-s) /(2600-1300) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<3300 && s>=2600: localStorage.setItem('one','5');localStorage.setItem('lv','Lv5');localStorage.setItem('two',parseInt(  (  (3300-s) /(3300-2600) )*10 ) );localStorage.setItem('wa', '');break;

                        case s<4000 && s>=3300: localStorage.setItem('one','4');localStorage.setItem('lv','Lv4');localStorage.setItem('two',parseInt(  (  (4000-s) /(4000-3300) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<4500 && s>=4000: localStorage.setItem('one','3');localStorage.setItem('lv','Lv3');localStorage.setItem('two',parseInt(  (  (4500-s) /(4500-4000) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<5000 && s>=4500: localStorage.setItem('one','2');localStorage.setItem('lv','Lv2');localStorage.setItem('two',parseInt(  (  (5000-s) /(5000-4500) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<5800 && s>=5000: localStorage.setItem('one','1');localStorage.setItem('lv','Lv1');localStorage.setItem('two',parseInt(  (  (5800-s) /(5800-5000) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<64000 && s>=5800: localStorage.setItem('one','0');localStorage.setItem('lv','Lv1');localStorage.setItem('two',randomNum(0,9));localStorage.setItem('wa', '');break;
                    }
                    if(   s<284 && parseInt(  (  (284-s + 40) /284)*10 ) >= 6 ) {
                        localStorage.setItem('lv','Lv10');localStorage.setItem('wa', '');
                    }
                    console.log(parseInt(  (  (284-s + 40) /284)*10) + '')
                    console.log(user_point_list)

                    localStorage.setItem('ms',['','','','','',
                        '','','','','',
                        '','','','','',
                        '','','','',''][randomNum(0,12)])

                    /*if(user_point_list<4) {
                        localStorage.setItem('one', '0');
                        localStorage.setItem('lv', 'Lv1');
                        localStorage.setItem('two', randomNum(0,9));
                    }
                    if(user_point_list>=4 && user_point_list<17) {
                        localStorage.setItem('one', '1');
                        localStorage.setItem('lv', 'Lv1');
                        localStorage.setItem('two', randomNum(0,9));
                    }*/

                    console.log(s)


                    //284 90
                    //750 80
                    //130070
                    //260060
                    //330050
                    //400040
                    //450030
                    //500020
                    //580010
                    //64000
                }
            }
        },
        mounted(){

            this.anmination()
            this.paperStart()

        }
    }
</script>

<style lang="scss" scoped>
    @function px2html($px){
        $rem: 37.5px;
        @return ($px / $rem) + rem;
    }

.main{
    height: 100%;
    width: 100%;
    position:fixed;
    .moon{
        position:absolute;
        top:px2html(150px);
        width:100%;
        height:px2html(350px);
    }
    .bottom-desc{
        color: #8B572A;
        font-size: px2html(18px);
        position: absolute;
        bottom:10vh;
        width:100vw;
        text-align: center;
    }
}
</style>
<style>
    .changBG{
        background: #fff7e4;
        font-size:20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
</style>